import React from 'react';
import './students.css';
import { connect } from 'react-redux'

import "react-table-6/react-table.css";
import { FaArrowLeft, FaAngleRight } from "react-icons/fa";
import iigpLogo from '../assets/iigpqm.png';

import Moment from 'react-moment'

import addImg from '../../assets/more (5).svg';
import imagepicker from '../../assets/photo.png';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';
import StudentTable from './studentTable';
import folderImg from '../assets/folderIcon.svg';
import {
    addStudent,
    showStudentInfo,
    editStudent,
    editStudentWithImage,
    deleteStudent
} from '../../../redux/actions/schoolAdminDashboard/schoolAdminActions';

class Students extends React.Component {
    constructor() {
        super();
        this.state = {
            showAdd: false,
            showDelete: false,
            showEdit: false,
            showView: false,
            name: "",
            dob: "",
            photo: [],
            address: "",
            blood: "",
            email: "",
            pname: "",
            pcontact: "",
            pemail: "",
            contact: '',
            class: "",
            section: "",
            city: "",
            state: "",
            zip: "",
            showImage: '',
            adding: false,
            deleting: false,
            editing: false,
            error: '',
            item: [],
            editname: "",
            editemail: "",
            editpemail: "",
            editphoto: [],
            editaddress: "",
            editblood: "",
            editdob: "",
            editcontact: '',
            editpemail: "",
            editclassName: "",
            editsectionName: "",
            editcity: "",
            editstate: "",
            editzip: "",
            editshowImage: '',
            imageEdited: false,
            showAssignSubject: false,
            showTable: false
        }
    }

    handleBox = (e,f) => {
        this.setState({
            current: {
                section:e,
                class:f
            },
            showTable: true
        })
    }

    handleBack=()=>{
        this.setState({
            showTable:false
        })
    }

    render() {
        var school = this.props.school;
        console.log(school);
        return (
            <div className="students-container">
                {!this.state.showTable ? (
                    <div className="students-header">
                        <div className="students-stats">
                            <h1>Class List</h1>
                        </div>
                    </div>
                ) : null}
                <div className="students-body">
                    {
                        !this.state.showTable
                            ?
                            <div className="chooseClass-container" style={{ padding: "0.5rem" }}>
                                {
                                    school && school.classes.map(cl => {
                                        return (
                                           <>
                                           {
                                               cl.sections.map(sec=>{
                                                   return(
                                                    <div className="classes-cardFolder-container" onClick={() => this.handleBox(sec,cl)}>
                                                        <div className="classes-cardFolder-img">
                                                            <img src={folderImg} alt="folder-icon" />
                                                        </div>
                                                        <div className="classes-cardFolder-name">
                                                            <p>{cl.name} {sec.name}</p>
                                                        </div>
                                                    </div>
                                                   )
                                               })
                                           }
                                           </>
                                        )
                                    })
                                }
                            </div>
                            :
                            <StudentTable school={school} sub={this.state.current} back={this.handleBack} />
                    }
                </div>
            </div>
        )
    }
}
function mapStateToProps(state) {
    return {
        school: state.schoolAdmin.school,
        schoolError: state.schoolAdmin.schoolError,
        schoolSucc: state.schoolAdmin.schoolSucc,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addStudent: (form_data, data) => dispatch(addStudent(form_data, data)),
        showStudentInfo: (data) => dispatch(showStudentInfo(data)),
        editStudentWithImage: (form_data, data) => dispatch(editStudentWithImage(form_data, data)),
        editStudent: (data) => dispatch(editStudent(data)),
        deleteStudent: (data) => dispatch(deleteStudent(data))
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Students);