import React, { Component } from "react";
import "./schoolProgress.css";

import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";

import FloralLoading from "../loadingScreen/floralLoading";

import attendance from "./assets/immigration.svg";


import Behavior from "./assets/feedback.svg";
import Abilities from "./assets/behavior.svg";
import homework from "./assets/homework.svg";
import Assignment from "./assets/document.svg";
import part from "./assets/collaboration.svg";
import gradebook from "./assets/gradebook.svg";
import exam from "./assets/exam.svg";
import Calender from "./assets/schedule.svg";
import Polls from "./assets/polling.svg";
import Announcement from "./assets/loudspeaker.svg";
import Forum from "./assets/discussion.svg";
import library from "./assets/library.svg";
import online from "./assets/lesson.svg";

import lab from "./assets/flask.svg";
import presentation from "./assets/flipchart.svg";
import seat from "./assets/seat.svg";

import { connect } from "react-redux";
import { fetch_student_profile_data, fetch_parent_profile_data } from "../../../redux/actions/parentDashboard/parentActions";
import { fetchUser } from "../../../redux/actions/authActions";

import MyAttendance from "./components/attendance/attendance.component";
import MyPolls from "./components/polls/polls.component";
import MyAnnouncements from "./components/announcements/announcements";
import MyCalendar from "./components/calendar/myCalendar";
import MyDiscussions from "./components/discussion/discussion";
import MySeating from "./components/seating/seating.component";
import MyLibrary from "./components/library/library.component";
import MyLabs from "./components/labs/labs.component";
import MyExams from "./components/exams/exams";
import MyBehaviour from "./components/behaviour/behaviour.component";
import MyAbilities from "./components/abilities/abilities";
import MyParticipation from "./components/participation/participation.component";
import MyAssignments from "./components/assignments/assignments";
import MyGradeBook from "./components/gradeBook/gradeBook";
import StudentFooter from "../websiteComponents/Footer/footer";
import MyHomeworks from "./components/homework/homework";

import ParentNavbar from '../websiteComponents/Navbar/parentNavbar.component';
import TopNavBar from '../websiteComponents/TopNav/topNav.component';
import ParentFooter from '../websiteComponents/Footer/footer';

class SchoolProgress extends Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            tab: "My Attendance",
            parentData: null,
            studentData: null
        };
    }

    componentDidMount = async () => {
        const currentUser = await this.props.fetchUser();
        if (currentUser) {
            const data = {
                parentId: currentUser.ref_id
            };

            const result = await this.props.fetch_parent_profile_data(data);

            if (result) {
                this.setState({
                    parentData: result
                }, () => {
                    this.handleGet();
                })
            }
        }


    };

    handleGet = async () => {

        const data = {
            studentId: this.props.match.params.studentId
        }
        const result = await this.props.fetch_student_profile_data(data);
        if (result) {
            this.setState({
                studentData: result,
                loading: false,
            });
        }
    }

    render() {
        return (
            <>
                {this.state.loading ? (
                    <FloralLoading />
                ) : (
                        <div className='myschool-container'>
                            <TopNavBar />
                            <ParentNavbar parent={this.state.parentData} currentStudent={this.props.match.params.studentId} />
                            <div className='myschool-subject'>
                                <p>My School / Learning Organizer</p>
                            </div>
                            <div className='myschool-activity'>
                                <OwlCarousel className='owl-theme' items={8} dots={false}>
                                    <div className='subject' onClick={() => this.setState({ tab: "My Attendance" })}>
                                        <img src={attendance} alt='img' />
                                        <p>Attendance</p>
                                    </div>
                                    {/* <div className='subject' onClick={() => this.setState({ tab: "My Library" })}>
                                        <img src={library} alt='img' />
                                        <p>Library</p>
                                    </div> */}

                                    {/* <div className='subject' onClick={() => this.setState({ tab: "My Lab Works" })}>
                                        <img src={lab} alt='img' />
                                        <p>Lab Work</p>
                                    </div> */}

                                    {/* {this.props.student.class.toLowerCase() === "grade 10" ||
								this.props.student.class.toLowerCase() === "grade 11" ||
								this.props.student.class.toLowerCase() === "grade 12" ? (
									put lab work here
								) : null} */}

                                    {/* <div className='subject' onClick={() => this.setState({ tab: "My Presentation" })}>
									<img src={presentation} alt='img' />
									<p>Presentation</p>
								</div> */}

                                    {/* <div className='subject' onClick={() => this.setState({ tab: "My Online Lessons" })}>
                                        <img src={online} alt='img' />
                                        <p>Online Lesson</p>
                                    </div> */}

                                    <div className='subject' onClick={() => this.setState({ tab: "My Exams" })}>
                                        <img src={exam} alt='img' />
                                        <p>Exams</p>
                                    </div>

                                    <div className='subject' onClick={() => this.setState({ tab: "My Calendar" })}>
                                        <img src={Calender} alt='img' />
                                        <p>Calendar</p>
                                    </div>

                                    {/* <div className='subject' onClick={() => this.setState({ tab: "My Polls" })}>
                                        <img src={Polls} alt='img' />
                                        <p>Polls</p>
                                    </div> */}

                                    <div className='subject' onClick={() => this.setState({ tab: "My Announcements" })}>
                                        <img src={Announcement} alt='img' />
                                        <p>Announcements</p>
                                    </div>

                                    {/* <div className='subject' onClick={() => this.setState({ tab: "My Discussions" })}>
                                        <img src={Forum} alt='img' />
                                        <p>Discussion</p>
                                    </div> */}

                                    {/* <div className='subject' onClick={() => this.setState({ tab: "My Seating Chart" })}>
                                        <img src={seat} alt='img' />
                                        <p>Seating Chart</p>
                                    </div> */}
                                    <div className='subject' onClick={() => this.setState({ tab: "My Behaviour" })}>
                                        <img src={Behavior} alt='img' />
                                        <p>Behaviour</p>
                                    </div>
                                    <div className='subject' onClick={() => this.setState({ tab: "My Skills" })}>
                                        <img src={Abilities} alt='img' />
                                        <p>Skills</p>
                                    </div>
                                    <div className='subject' onClick={() => this.setState({ tab: "My Assignments" })}>
                                        <img src={Assignment} alt='img' />
                                        <p>Assignments</p>
                                    </div>
                                    <div className='subject' onClick={() => this.setState({ tab: "My Homework" })}>
                                        <img src={homework} alt='img' />
                                        <p>Homework</p>
                                    </div>
                                    <div className='subject' onClick={() => this.setState({ tab: "My Participation" })}>
                                        <img src={part} alt='img' />
                                        <p>Participation</p>
                                    </div>
                                    <div className='subject' onClick={() => this.setState({ tab: "My Grade Book" })}>
                                        <img src={gradebook} alt='img' />
                                        <p>Grade Book</p>
                                    </div>
                                </OwlCarousel>
                            </div>

                            <div className='learning-grade-breadcrumb'>
                                <p>{this.state.tab}</p>
                            </div>

                            <div className='myschool-tabs'>
                                {/* My Attendance */}
                                {this.state.tab === "My Attendance" ? <MyAttendance attendance={this.props.student.attendance} /> : null}

                                {/* My Library */}
                                {/* {this.state.tab === "My Library" ? <MyLibrary /> : null} */}

                                {/* My Polls */}
                                {/* {this.state.tab === "My Polls" ? <MyPolls polls={this.props.student.polls} /> : null} */}

                                {/* My Announcements */}
                                {this.state.tab === "My Announcements" ? (
                                    <MyAnnouncements announcements={this.props.student.announcements} studentId={this.props.student._id} />
                                ) : null}

                                {/* My Calendar */}
                                {this.state.tab === "My Calendar" ? <MyCalendar calendarEvents={this.props.student.calendarEvents} /> : null}

                                {/* My Discussions */}

                                {/* {this.state.tab === "My Discussions" ? <MyDiscussions student={this.props.student} /> : null} */}

                                {/* My Seating Chart */}

                                {/* {this.state.tab === "My Seating Chart" ? <MySeating student={this.props.student} /> : null} */}

                                {/* My Labs */}

                                {/* {this.state.tab === "My Lab Works" ? <MyLabs student={this.props.student} /> : null} */}

                                {/* My Exams */}

                                {this.state.tab === "My Exams" ? <MyExams student={this.props.student} /> : null}
                                {/* My Behaviour */}
                                {this.state.tab === "My Behaviour" ? <MyBehaviour behaviour={this.props.student.behaviour} /> : null}

                                {/* My Abilities */}
                                {this.state.tab === "My Skills" ? (
                                    <MyAbilities
                                        creativity={this.props.student.creativity}
                                        problem={this.props.student.problemSolving}
                                        entrepreneurship={this.props.student.entrepreneurship}
                                        setSubTab={(value) => this.setState({ subTab: value })}
                                    />
                                ) : null}

                                {/* My Participation */}
                                {this.state.tab === "My Participation" ? <MyParticipation participation={this.props.student.participation} /> : null}

                                {/* My Assignments */}
                                {this.state.tab === "My Assignments" ? <MyAssignments assignments={this.props.student.assignments} /> : null}

                                {/* My Homeworks */}

                                {this.state.tab === "My Homework" ? <MyHomeworks /> : null}

                                {/* My Grade Book */}
                                {this.state.tab === "My Grade Book" ? <MyGradeBook setSubTab={(value) => this.setState({ subTab2: value })} student={this.props.student} /> : null}
                            </div>

                            <ParentFooter />
                        </div>
                    )}
            </>
        );
    }
}

function mapStateToProps(state) {
    return {
        auth: state.auth,
        parent: state.parent.profileData,
        student: state.parent.studentProfile
    };
}

function mapDispatchToProps(dispatch) {
    return {
        fetchUser: () => dispatch(fetchUser()),
        fetch_parent_profile_data: (parentData) => dispatch(fetch_parent_profile_data(parentData)),
        fetch_student_profile_data: (studentData) => dispatch(fetch_student_profile_data(studentData))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SchoolProgress);
