import React from 'react';

import './examSubjectAnalytics.css';

import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

import { Link } from 'react-router-dom';

import maths from '../assets/math.svg'; import science from '../assets/science.svg'; import english from '../assets/english.svg'; import islamic from '../assets/islamic.svg'; import computer from '../assets/computer.svg'; import skills from '../assets/skills.svg'; import arts from '../assets/arts.svg'; import sports from '../assets/sports.svg'; import music from '../assets/music-note.svg'; import social from '../assets/social.svg';
import FloralLoading from '../../loadingScreen/floralLoading';

import { BsLaptop, BsClipboard, BsListOl, BsGraphUp } from 'react-icons/bs'
import ScoresList from './components/scores/scores';
import QuestionLogs from './components/questionLogs/questionLogs';

import { connect } from 'react-redux';
import { fetchUser } from '../../../../redux/actions/authActions';
import { fetch_student_profile_data, fetch_parent_profile_data } from '../../../../redux/actions/parentDashboard/parentActions';

import axios from 'axios';
import ParentNavbar from '../../websiteComponents/Navbar/parentNavbar.component';
import TopNavBar from '../../websiteComponents/TopNav/topNav.component';
import ParentFooter from '../../websiteComponents/Footer/footer';

class ExamSubjectAnalyticsParent extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            tab: 1,
            analyticsData: [],
            subject: '',
            parentData: null,
            studentData: null
        }
    }

    componentDidMount = async () => {
        const currentUser = await this.props.fetchUser();
        if (currentUser) {
            const data = {
                parentId: currentUser.ref_id
            };

            const result = await this.props.fetch_parent_profile_data(data);

            if (result) {
                this.setState({
                    parentData: result
                }, () => {
                    this.handleGet();
                })
            }
        }
    }

    handleGet = async () => {
        this.setState({
            tab: 0,
            loading: true
        })

        const d = {
            studentId: this.props.match.params.studentId
        }

        const studentData = await this.props.fetch_student_profile_data(d);
        if (studentData) {

            const data = {
                studentId: this.props.match.params.studentId,
                subject: this.props.match.params.subject
            }
            var response = await axios.post("/api/parent/examAnalytics/subject", data);
            if (response.data !== null) {
                if (response.data.type === 'success') {
                    this.setState({
                        analyticsData: response.data.data,
                        subject: this.props.match.params.subject,
                        studentData: studentData,
                        loading: false,
                        tab: 1
                    })
                }
            }
            else {
                this.setState({
                    subject: this.props.match.params.subject,
                    studentData: studentData,
                    loading: false
                })
            }
        }
    }

    render() {

        return (
            <>
                {
                    this.state.loading
                        ?
                        <FloralLoading />
                        :
                        <div className="sub-analytics-container">
                            <TopNavBar />
                            <ParentNavbar parent={this.state.parentData} currentStudent={this.props.match.params.studentId} />
                            <div className="learning-breadcrumb">
                                <p>{this.state.studentData.name} - Learning Analysis / {this.props.match.params.subject.substring(0, 1).toUpperCase() + this.props.match.params.subject.substring(1, this.props.match.params.subject.length)}</p>
                            </div>
                            <div className="learning-choose-subject-breadcrumb">
                                <OwlCarousel className="owl-theme" items={7} dots={false}>
                                    <a
                                        href={"/dashboard/parent/examAnalytics/mathematics/" + this.props.match.params.studentId}
                                        className="subject-option"
                                    >
                                        <img src={maths} alt="subject" />
                                        <p>Mathematics</p>
                                    </a>

                                    <a
                                        href={"#"}
                                        className="subject-option"
                                    >
                                        <img src={science} alt="subject" />
                                        <p>Science</p>
                                    </a>

                                    <a href={"#"} className="subject-option">
                                        <img src={english} alt="subject" />
                                        <p>English</p>
                                    </a>

                                    <a href={"#"} className="subject-option">
                                        <img src={islamic} alt="subject" />
                                        <p>Islamic Studies</p>
                                    </a>

                                    <a href={"#"} className="subject-option">
                                        <img src={social} alt="subject" />
                                        <p>Social Studies</p>
                                    </a>

                                    <a href={"#"} className="subject-option">
                                        <img src={computer} alt="subject" />
                                        <p>Information Technology</p>
                                    </a>

                                    <a href={"#"} className="subject-option">
                                        <img src={skills} alt="subject" />
                                        <p>Life Skills</p>
                                    </a>

                                    <a href={"#"} className="subject-option">
                                        <img src={sports} alt="subject" />
                                        <p>Sports</p>
                                    </a>

                                    <a href={"#"} className="subject-option">
                                        <img src={music} alt="subject" />
                                        <p>Music</p>
                                    </a>

                                    <a href={"#"} className="subject-option">
                                        <img src={arts} alt="subject" />
                                        <p>Arts</p>
                                    </a>
                                </OwlCarousel>
                            </div>
                            <div className="select-analytics-type">
                                <div className={this.state.tab === 1 ? "tab-type active" : "tab-type"} onClick={() => this.setState({ tab: 1 })}>
                                    <h1><BsClipboard /></h1>
                                    <p>SCORES</p>
                                </div>
                                <div className={this.state.tab === 2 ? "tab-type active" : "tab-type"} onClick={() => this.setState({ tab: 2 })}>
                                    <h1><BsListOl /></h1>
                                    <p>QUESTIONS</p>
                                </div>
                            </div>
                            <div className="report-whiteboard">
                                {
                                    this.state.tab === 1
                                        ?
                                        <ScoresList Adata={this.state.analyticsData} />
                                        :
                                        <QuestionLogs Adata={this.state.analyticsData} />
                                }
                            </div>
                            <ParentFooter />
                        </div>
                }
            </>
        )
    }
}

function mapStateToProps(state) {
    return {
        auth: state.auth,
        parent: state.parent.profileData,
        student: state.parent.studentProfile
    };
}

function mapDispatchToProps(dispatch) {
    return {
        fetchUser: () => dispatch(fetchUser()),
        fetch_parent_profile_data: (parentData) => dispatch(fetch_parent_profile_data(parentData)),
        fetch_student_profile_data: (studentData) => dispatch(fetch_student_profile_data(studentData))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ExamSubjectAnalyticsParent);