import { FETCH_PARENT_PROFILE, FETCH_STUDENT_PROFILE } from '../actions/parentDashboard/parentTypes';

const initialState = {
    profileData: null,
    studentProfile: null
}

const parentReducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_PARENT_PROFILE:
            return {
                profileData: action.payload,
                studentProfile: state.profileData
            }
        case FETCH_STUDENT_PROFILE:
            return {
                profileData: state.profileData,
                studentProfile: action.payload
            }
        default:
            return state
    }
}

export default parentReducer;