import React from 'react';

import './childCard.css';

import { Spinner } from "react-bootstrap";

import axios from 'axios';

class ChildCard extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            studentDetails: {},
            nf: false
        }
    }

    componentDidMount = async () => {
        const data = {
            studentId: this.props.studentId.id
        }

        var response = await axios.post("/api/parent/getStudentProfile", data);

        if (response.data !== null) {
            if (response.data.error) {
                this.setState({
                    loading: false,
                    nf: true
                })
            }
            else {
                this.setState({
                    studentDetails: response.data,
                    loading: false
                })
            }
        }
    }

    render() {
        return (
            <>
                {
                    this.state.nf
                        ?
                        null
                        :
                        <div className="child-card-container" onClick={() => this.props.selectedId(this.state.studentDetails._id)}>
                            {
                                this.state.loading
                                    ?
                                    <Spinner animation='border' role='status' variant='primary'>
                                        <span className='sr-only'>Loading...</span>
                                    </Spinner>
                                    :
                                    <>
                                        <img src={this.state.studentDetails.photo ? "/api/file/show/" + this.state.studentDetails.photo.filename : ''} alt="photo" />
                                        <p>{this.state.studentDetails.name}</p>
                                    </>
                            }
                        </div>
                }
            </>
        )
    }
}

export default ChildCard;