import React from 'react';

import './parentNavbar.style.css';

import { Link } from "react-router-dom";

import logo from "./assets/iigp.png";

import { MdKeyboardArrowDown } from 'react-icons/md';
import StudentTab from './studentTab/studentTab';

class ParentNavbar extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            showMenu: false,
            showProfile: false
        }
    }

    render() {

        return (
            <div className="parent-navbar-container">
                <div className="iigp-logo">
                    <Link to={"/dashboard"}>
                        <img src={logo} alt="iigp-logo" />
                    </Link>
                </div>
                <div className="menu-links">

                    <a href="/dashboard" className="link-box">
                        <p>PRACTICE ANALYTICS</p>
                    </a>

                    <a href={"/dashboard/parent/examAnalytics/" + this.props.currentStudent} className="link-box">
                        <p>EXAM ANALYTICS</p>
                    </a>

                    <a href={"/dashboard/parent/schoolInfo/" + this.props.currentStudent} className="link-box">
                        <p>SCHOOL</p>
                    </a>

                    <div className="link-user" onClick={() => this.setState({ showMenu: !this.state.showMenu })} >
                        <p>Welcome, Parent</p>
                        <span><MdKeyboardArrowDown /></span>
                    </div>

                    <div className={this.state.showMenu ? "nav-menu active" : "nav-menu"}>
                        {
                            this.props.parent.children.length > 1 && this.props.parent.children.map((item) => {
                                return (
                                    <div className="menu-list">
                                        <StudentTab studentId={item.id} />
                                    </div>
                                )
                            })
                        }
                        <div className="menu-list">
                            <a href="/api/logout">Logout</a>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

export default ParentNavbar;