import React from 'react';

import './quesList.css';
import { FiPlus } from 'react-icons/fi';
import { TiTick } from 'react-icons/ti';
import { RiCloseLine } from 'react-icons/ri';

import MathJax from 'react-mathjax-preview';

class QuesList extends React.Component {

    constructor(props) {
        super(props);
        this.accordionContent = [];
    }

    handleAcc = (e) => {
        var panel = this.accordionContent[e].nextElementSibling;
        if (panel.style.display === "flex") {
            panel.style.display = "none";
        }
        else {
            panel.style.display = "flex";
        }
    }

    render() {
        return (
            <div className="question-accordion">
                <div className="accordion" ref={accordionContent => this.accordionContent[0] = accordionContent} onClick={() => this.handleAcc(0)}>
                    <div className="left-acc">
                        <h1>SECTION : {this.props.list.name} </h1>
                    </div>
                    <div className="right-acc">
                        <div className="plus">
                            <h1><FiPlus /></h1>
                        </div>
                    </div>
                </div>

                <div className="panel">
                    {
                        this.props.list.questions && this.props.list.questions.length > 0
                            ?
                            this.props.list.questions.map((item) => {
                                return (
                                    <div className="panel-topic">
                                        <MathJax math={item.question} />
                                        <div className="correct">
                                            <p>Correct Answer : </p>
                                            {
                                                item.options.map((option) => {
                                                    if (option.answer === true) {
                                                        return <span><MathJax math={option.value} /></span>
                                                    }
                                                })
                                            }

                                        </div>
                                        <div className="actual">
                                            <p>Your Answer : </p>
                                            {
                                                item.options.map((option) => {
                                                    if (option.selected === true) {
                                                        if (option.selected === option.answer) {
                                                            return (
                                                                <>
                                                                    <span><MathJax math={option.value} /></span>
                                                                    <h1 id="tick"><TiTick /></h1>
                                                                </>
                                                            )
                                                        }
                                                        else {
                                                            return (
                                                                <>
                                                                    <span><MathJax math={option.value} /></span>
                                                                    <h1 id="wrong"><RiCloseLine /></h1>
                                                                </>
                                                            )
                                                        }
                                                    }
                                                })
                                            }
                                        </div>
                                    </div>
                                )
                            })
                            :
                            null
                    }
                </div>

            </div>
        )
    }
}

export default QuesList;