import React from 'react';

import './studentTab.style.css';

import axios from 'axios';

class StudentTab extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            studentDetails: null,
            nf: false
        }
    }

    componentDidMount = async () => {
        const data = {
            studentId: this.props.studentId
        }

        var response = await axios.post("/api/parent/getStudentProfile", data);

        if (response.data !== null) {
            if (response.data.error) {
                this.setState({
                    nf: true,
                    loading: false
                })
            }
            else {
                this.setState({
                    studentDetails: response.data,
                    loading: false
                })
            }
        }
    }

    render() {
        return (
            <>
                {
                    this.state.nf
                        ?
                        null
                        :
                        <>
                            {
                                this.state.loading
                                    ?
                                    null
                                    :
                                    <div className="student-tab-list">
                                        <p>{this.state.studentDetails.name}</p>
                                    </div>
                            }
                        </>
                }
            </>
        )
    }
}

export default StudentTab;