import React from 'react';
import './quesSessions.css';

import { IoMdArrowBack } from 'react-icons/io';
import { BsPencil, BsClock, BsStar } from 'react-icons/bs';

import Chart from "react-apexcharts";
import JuicerLoading from '../../juicerLoading/juicerLoading';


import QuesList from '../quesList/quesList';

class QuesSessions extends React.Component {

    constructor(props) {
        super(props);
        this.accordionContent = [];
        this.state = {
            loading: true,
            ques: {},
            sessionList: [],
            totalQues: 0,
            totalMarks: 0
        }
    }

    componentDidMount() {
        console.log(this.props.report);

        this.setState({
            sessionList: this.props.report.questions,
            totalQues: this.props.report.correct + this.props.report.wrong,
            totalMarks: this.props.report.marks,
            loading: false
        })
    }

    handleAcc = (e) => {
        var panel = this.accordionContent[e].nextElementSibling;
        if (panel.style.display === "flex") {
            panel.style.display = "none";
        }
        else {
            panel.style.display = "flex";
        }
    }

    render() {
        return (
            <>
                {
                    this.state.loading
                        ?
                        <JuicerLoading />
                        :
                        <div className="quesSession-board">
                            <div className="quesSession-head">
                                <h1 id="back-arrow" onClick={() => this.props.backToQuestions(false)}><IoMdArrowBack /></h1>
                                <h1>BACK TO QUESTION LOGS</h1>
                            </div>

                            <div className="quesSession-stats-summary">
                                <div className="summary-head">
                                    <h1>Exam Summary</h1>
                                </div>
                                <div className="summary-body">
                                    <div className="summary-body-top">

                                        <div className="stats-box">
                                            <div className="left">
                                                <p id="star"><BsStar /></p>
                                            </div>
                                            <div className="right">
                                                <p>CURRENT</p>
                                                <h1>{this.state.totalMarks}</h1>
                                                <p>MARKS</p>
                                            </div>
                                        </div>
                                        <div className="stats-box">
                                            <div className="left">
                                                <p id="pencil"><BsPencil /></p>
                                            </div>
                                            <div className="right">
                                                <p>TOTAL</p>
                                                <h1>{this.state.totalQues}</h1>
                                                <p>QUESTIONS</p>
                                            </div>
                                        </div>
                                        <div className="stats-box">
                                            <div className="left">
                                                <p id="clock"><BsClock /></p>
                                            </div>
                                            <div className="right">
                                                <p>TOTAL</p>
                                                <h1>{this.props.report.correct}</h1>
                                                <p>CORRECT</p>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </div>

                            <div className="quesSession-questions">
                                <div className="questions-head">
                                    <h1>Questions Attempted</h1>
                                </div>

                                <div className="questions-body">

                                    {
                                        this.state.sessionList && this.state.sessionList.map((item, index) => {
                                            return <QuesList list={item} index={index + 1} />
                                        })
                                    }

                                </div>
                            </div>
                        </div>
                }
            </>
        )
    }
}

export default QuesSessions;