import React from 'react';

import './questionLogs.css';

import JuicerLoading from '../juicerLoading/juicerLoading';

import { TiArrowSortedDown } from 'react-icons/ti';
import { BsPlusCircle } from 'react-icons/bs';
import QuesSessions from './quesSessions/quesSessions';

class QuestionLogs extends React.Component {
    constructor(props) {
        super(props);
        this.accordionContent = [];
        this.state = {
            loading: true,
            showSession: false,
            filteredData: [],
            dataArray: []
        }
    }

    componentDidMount() {
        var report = this.props.Adata;

        var filteredData = [], visitedChapter = [], visitedTopic = [];
        for (var i = 0; i < report.length; i++) {
            console.log(report[i]);
            if (visitedChapter.includes(report[i].chapter) === false) {
                console.log("NEW CHAPTER");
                visitedChapter.push(report[i].chapter);
                visitedTopic.push(report[i].topic);

                var chap = {}, topicDetails = {};
                chap.chapterName = report[i].chapter
                chap.topic = [];
                topicDetails.topicName = report[i].topic;
                topicDetails.array = [];
                topicDetails.array.push(report[i]);
                chap.topic.push(topicDetails);

                filteredData.push(chap);
            }
            else {
                console.log("OLD CHAPTER");
                if (visitedTopic.includes(report[i].topic) === false) {
                    console.log("NEW TOPIC")
                    visitedTopic.push(report[i].topic);
                    filteredData.map((item) => {
                        if (item.chapterName === report[i].chapter) {
                            var topicDetails = {};
                            topicDetails.topicName = report[i].topic;
                            topicDetails.array = [];
                            topicDetails.array.push(report[i]);

                            item.topic.push(topicDetails);
                        }
                    })

                }
                else {
                    console.log("OLD TOPIC");
                    filteredData.map((item) => {
                        if (item.chapterName === report[i].chapter) {
                            item.topic.map((e) => {
                                if (e.topicName === report[i].topic) {
                                    e.array.push(report[i])
                                }
                            })
                        }
                    })
                }
            }
        }

        console.log(filteredData);
        this.setState({
            filteredData: filteredData,
            loading: false
        })
    }

    handleTopicSelect = (array) => {
        this.setState({
            dataArray: array,
            showSession: true
        })
    }

    handleChangeSession = (value) => {
        this.setState({
            showSession: value
        })
    }

    handleAcc = (e) => {
        var panel = this.accordionContent[e].nextElementSibling;
        if (panel.style.display === "flex") {
            panel.style.display = "none";
        }
        else {
            panel.style.display = "flex";
        }
    }

    render() {
        return (
            <div className="questionLogs-container">
                {
                    this.state.loading
                        ?
                        <JuicerLoading />
                        :
                        <>
                            {
                                !this.state.showSession
                                    ?
                                    <div className="questionLogs-board">
                                        <div className="questionLogs-head">
                                            <h1>QUESTION LOGS</h1>
                                            <p>( Select any topic from below )</p>
                                        </div>
                                        <div className="questionLogs-body">

                                            <div className="questionLogs-table-body">

                                                {
                                                    this.state.filteredData && this.state.filteredData.map((item, index) => {
                                                        return (
                                                            <div className="questionLogs-accordion">
                                                                <div class="accordion" ref={accordionContent => this.accordionContent[index] = accordionContent} onClick={() => this.handleAcc(index)}>
                                                                    <h1><TiArrowSortedDown /></h1>
                                                                    <p>{item.chapterName}</p>
                                                                </div>
                                                                <div className="panel">
                                                                    {
                                                                        item.topic.map((e) => {
                                                                            return (
                                                                                <div className="panel-topic" onClick={() => this.handleTopicSelect(e.array)}>
                                                                                    <p>{e.topicName}</p>
                                                                                    <h1><BsPlusCircle /></h1>
                                                                                </div>
                                                                            )
                                                                        })
                                                                    }
                                                                </div>
                                                            </div>
                                                        )
                                                    })
                                                }

                                            </div>
                                        </div>
                                    </div>
                                    :
                                    <QuesSessions report={this.state.dataArray} backToQuestions={(value) => this.handleChangeSession(value)} />
                            }
                        </>
                }
            </div>
        )
    }
}

export default QuestionLogs;